import React,{Component , Suspense} from 'react';
import styles from './App.module.css';
import data from './data/data';
const Search = React.lazy(()=>import('./components/Search/Search'));
const Data = React.lazy(()=>import('./components/Data/Data'));
const Navbar = React.lazy(()=>import('./components/Navbar/Navbar'));

class App extends Component  {

  state = {
    data : {},
    dataShow : false,
    sideToggle : false,
    searchInput : '',
    filters : {
      Persons : true,
      Matches : true,
      Teams : true,
    }
  }

  toggleHandler = ()=>{
    this.setState({sideToggle : !this.state.sideToggle});
  }
  onSearchHandler = async (event)=>{
    event.preventDefault();
    let newData = JSON.parse(JSON.stringify(data));
    let allCheck = false;
    const filters = Object.keys(this.state.filters).map(key=>{
      if(!this.state.filters[key]){
        delete newData.data[key]
      }else{
        allCheck = true;
      }
      return key;
    });
    if(!allCheck){
      newData = JSON.parse(JSON.stringify(data));
    }
    await this.setState({ dataShow : true , data : newData});
  }
  onChangeHandler = async (event)=>{
    await this.setState({searchInput : event.target.value});
  }
  filterButtonClickHandler = async (event)=>{
    const filterType = event.target.value;
    let filters = {...this.state.filters};
    filters[filterType] = !filters[filterType];
    await this.setState({filters : filters});
    this.onSearchHandler(event);
  }
  render(){
    return (
      <div className="App">
        {/* for lazy loading */}
        <Suspense fallback={<div>Loading...</div>}>
          {/* Navbar */}
        <Navbar toggle={this.state.sideToggle} toggleHandler = {this.toggleHandler} />
        <div className={styles.Layout}> 
        {/* Search Component */}
          <Search 
                  onChangeHandler={this.onChangeHandler} 
                  inputValue = {this.state.searchInput}
                  onSearchHandler={this.onSearchHandler}
                  filterButtonClickHandler = {this.filterButtonClickHandler}
                  filters = {this.state.filters}
                   />
          {(this.state.dataShow === true) ? <Data data={this.state.data} /> : null}
          
        </div>
        </Suspense>
      </div>
    );
  }
}

export default App;
